<script>
import rmzy from "./rmzy";
export default {
  components: { rmzy },
  name: "xyfg",
  data() {
    return {
      xyfgda: [],
      routerList: {},
      pageOption: {
        pageIndex: 1,
        pageTotal: 0,
        pageSize: 8,
      },
      pageRouterList: 0,
    };
  },
  created() {
    var _this = this;
    _this.pageRouterList = _this.$route.query.cid;
    _this.getDATA();
  },
  methods: {
    getDATA() {
      var _this = this;

      var temp = _this.Base.GetFilesInfoByCid(
        "",
        _this.pageRouterList,
        "",
        "",
        _this.pageOption.pageIndex,
        "",
        _this.pageOption.pageSize
      );
      _this.xyfgda = temp.GetData;

      _this.pageOption.pageTotal = temp.Total;
    },
    pageF: function (data) {
      this.pageOption.pageIndex = data;
      this.getDATA();
    },
  },
  mounted() {
    var _that = this;

    _that.getDATA();
  },
};
</script>
<style  scoped>
.xyfg {
