  height: 353px;
  float: left;
}
.xyfcb .xyfgcon li:nth-child(2n) {
  margin: 0 0 40px 20px;
}
.xyfcb .xyfgcon img {
  width: 100%;
  height: 290px;
  transition: all 0.4s;
}
.xyfcb .xyfgcon p {
  width: 100%;
  height: 60px;
  font-family: MicrosoftYaHei;
  font-size: 24px;
  line-height: 60px;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0px;
  color: #333333;
  text-align: center;
  background-color: #ffffff;
  box-shadow: 0px 4px 7px 0px rgba(0, 0, 0, 0.05);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.xyfcb .rmzyB {
  width: 260px;
}
.xyfcb .xyfgcon li:hover img {
  width: 101%;
  height: 295px;
}
</style>



